import LineGradient from '../components/LineGradient';
import { motion } from 'framer-motion';
import { proj1, proj2, proj3, proj4, proj5, proj6, proj7 } from './index';



const container = {
    hidden: {},
    visible: {
        transition: { staggerChildren: 0.2 }
    }
}

const projectVariant = {
    hidden: { opacity: 0, scale:0.8 },
    visible: { opacity:1, scale:1 }
}


const Project = ({title, image}) => {
    const overlayStyles = "absolute h-full w-full opacity-0 hover:opacity-90 transition duration-500 bg-grey z-30 flex flex-col justify-center items-center text-center p-16 text-deep-blue "
    return (
      <motion.div variants={projectVariant} className="relative">
        <div className={overlayStyles}>
          <p className="text-2xl font-playfair">{title}</p>
          <p className="mt-7">
            As a software developer specializing in web development and data
            structures, I possess a diverse skill set.
          </p>
        </div>
        <img src={image} alt="imagesOOPS!"/>
      </motion.div>
    );
}


const Projects = () => {
    return (
      <section id="projects" className="pt-20  md:pt-48 pb-48">
        <motion.div
          className="md:w-2/4 mx-auto text-center"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: -50 },
            visible: { opacity: 1, y: 0 },
          }}
        >
          <div>
            <p className="font-playfair font-semibold text-4xl">
              PRO<span className="text-light-blue">JECTS</span>
            </p>
            <div className="flex justify-center mt-5">
              <LineGradient width="w-1/3" />
            </div>
          </div>
          <p className="mt-10 mb-10">
            As a software developer specializing in web development and data
            structures, I possess a diverse skill set that enables me to design
            and implement high-quality software solutions.
          </p>
        </motion.div>

        <div className="flex justify-center">
          <motion.div
            className="sm:grid sm:grid-cols-3"
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            variants={container}
          >
            <div className="flex justify-center text-center items-center p-10 bg-red max-w-[400px] max-h-[400px] text-2xl font-play-fair font-semibold">
              Beautiful User Interfaces
            </div>
            <Project title="Project 1" image={`${proj1}`} />
            <Project title="Project 2" image={`${proj2}`} />
            <Project title="Project 3" image={`${proj3}`} />
            <Project title="Project 4" image={`${proj4}`} />
            <Project title="Project 5" image={`${proj5}`} />
            <Project title="Project 6" image={`${proj6}`} />
            <Project title="Project 7" image={`${proj7}`} />
            <div className="flex justify-center text-center items-center p-10 bg-blue max-w-[400px] max-h-[400px] text-2xl font-play-fair font-semibold">
              Smooth User Interfaces
            </div>
          </motion.div>
        </div>
      </section>
    );
}

export default Projects;