import { motion } from "framer-motion";
import AnchorLink from "react-anchor-link-smooth-scroll";
import {heroimg } from ".";
import SocialMediaIcons from "../components/SocialMediaIcons";

const Landing = ({ setselectedPage }) => {
  return (
    <section
      id="home"
      className="md:flex md:justify-between md:items-center md:h-full gap-16 py-10"
    >
      {/* {image section} */}
      <div className="md:order-2 flex justify-center basis-3/5 z-10 mt-16 md:mt-32 md:max-h-[620px]">
        <div className="z-0">
          <img
            src={heroimg}
            alt="profile"
            className="hover:filter hoverL:saturate-200 transition duration-500 z-10 w-full max-w-[400px] md:max-w-[600px] h-full"
          />
        </div>
      </div>
      {/* {main section} */}
      <div className="z-30 basis-2/5 mt-12 md:mt-32 md:ml-20">
        <motion.div
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: -200 },
            visible: { opacity: 1, y: 0 },
          }}
        >
          <p className="text-4xl md:text-5xl font-playfair z-10 text-center md:text-start ">
            Melakeselam {""}
            <span className=" md:text-white xs:font-semibold md:p-4 py-1 rounded-tl-[120px] rounded-br-[120px] my-20 md:rounded-tl-[65px] md:rounded-br-[65px] md:bg-gradient-background text-4xl md:text-5xl bg-gradient-background">
              Yitbarek
            </span>
          </p>
          <p className="mt-10 mb-7 text-sm text-center md:text-start">
            A Junior software engineer and developer and mostly <br />
            working on web development, Data Structures and algorithm.
          </p>
        </motion.div>

        <motion.div
          className="flex mt-5 justify-center md:justify-start"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: -100 },
            visible: { opacity: 1, x: 0 },
          }}
        >
          <AnchorLink
            className="bg-gradient-rainblue text-deep-blue rounded-sm py-3 px-7 font-semibold hover:bg-blue hover:text-white transition duration-500"
            onClick={() => setselectedPage("contact")}
            href="#contact"
          >
            Contact me
          </AnchorLink>
          <AnchorLink
            className="bg-gradient-rainblue py-0.5 pr-0.5"
            onClick={() => setselectedPage("contact")}
            href="#contact"
          >
            <div className="bg-light-blue text-white hover:text-red transition duration-500 w-full h-full flex items-center justify-center font-playfair px-10">
              Let's talk
            </div>
          </AnchorLink>
        </motion.div>

        <motion.div
          className="flex mt-5 justify-center md:justify-start"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ delay: 0.4, duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: 100 },
            visible: { opacity: 1, x: 0 },
          }}
        >
          <SocialMediaIcons />
        </motion.div>
      </div>
    </section>
  );
};

export default Landing;
