import {
  AiFillGithub,
  AiFillTwitterCircle,
  AiFillLinkedin,
  AiFillFacebook,
} from "react-icons/ai";

const SocialMediaIcons = () => {
  return (
    <div className="flex justify-center md:justify-start my-10 gap-7">
      <a
        className="hover:opacity-50 trasition duration-500  "
        href="https://twitter.com"
        target="_blank"
        rel="noreferrer"
      >
        <AiFillTwitterCircle size={30} />
      </a>
      <a
        className="hover:opacity-50 trasition duration-500  "
        href="https://facebook.com"
        target="_blank"
        rel="noreferrer"
      >
        <AiFillFacebook size={30} />
      </a>
      <a
        className="hover:opacity-50 trasition duration-500  "
        href="https://www.linkedin.com/in/melakeselam-yitbarek-739122254/"
        target="_blank"
        rel="noreferrer"
      >
        <AiFillLinkedin size={30} />
      </a>
      <a
        className="hover:opacity-50 trasition duration-500  "
        href="https://github.com/melegithubyit"
        target="_blank"
        rel="noreferrer"
      >
        <AiFillGithub size={30} />
      </a>
    </div>
  );
};

export default SocialMediaIcons;
