import LineGradient from "../components/LineGradient";
import { useForm } from 'react-hook-form';
import {motion} from 'framer-motion';
import { contactImage } from "./index";
import { mailimage } from "./index";

const Contact = () => {
    const { register, trigger, formState:{ errors } } = useForm();

    const onSubmit = async (e) => {
        const isValid = await trigger();
        if (!isValid) {
            e.preventDefault();
        }
    }

    return (
      <section id="contact" className="pb-40 pt-20 md:py-48">
        <motion.div
          className="flex justify-end w-full"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: -50 },
            visible: { opacity: 1, x: 0 },
          }}
        >
          <div className="relative h-32">
            <p className="font-playfair font-semibold text-4xl mb-5 text-deep-blue text-center">
              <span className="text-light-blue">Get In Touch</span> With Me
            </p>
            <div className="flex justify-center md:justify-end my-5">
              <LineGradient width="w-3/4" />
            </div>
          </div>
        </motion.div>

        <div className="md:flex md:justify-between gap-16 mt-5">
          <motion.div
            className="basis-1/3 mt-10 md:mt-0"
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.5 }}
            variants={{
              hidden: { opacity: 0, y: 50 },
              visible: { opacity: 1, y: 0 },
            }}
          >
            <img src={mailimage} alt="contact" />
          </motion.div>

          <motion.div
            className="basis-2/3 mt-10 md:mt-0"
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            transition={{ delay: 0.2, duration: 0.5 }}
            variants={{
              hidden: { opacity: 0, y: 50 },
              visible: { opacity: 1, y: 0 },
            }}
          >
            {/* main: f0b2efe559d9b9b36b9f2691d1a7490b */}
            {/* melakeselamyit: 3f401add17022c752cd909ffde7936ad */}
            <form
              target="_blank"
              onSubmit={onSubmit}
              action="https://formsubmit.co/3f401add17022c752cd909ffde7936ad"
              method="POST"
            >
              <input
                className="w-full border-2 border-deep-grey font-semibold placeholder-opaque-black p-3"
                type="text"
                placeholder="NAME"
                {...register("name", {
                  required: true,
                  maxLength: 100,
                })}
              />
              {errors.name && (
                <p className="text-red mt-1">
                  {errors.name.type === "required" && "This field is Required"}
                  {errors.name.type === "maxLength" && "Max length is 100 char"}
                </p>
              )}
              <input
                className="w-full border-2 border-deep-grey font-semibold placeholder-opaque-black p-3 mt-3"
                type="text"
                placeholder="EMAIL"
                {...register("email", {
                  required: true,
                  pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                })}
              />
              {errors.email && (
                <p className="text-red mt-1">
                  {errors.email.type === "required" && "This field is Required"}
                  {errors.email.type === "pattern" && "invalid input"}
                </p>
              )}
              <textarea
                className="w-full border-2 border-deep-grey font-semibold placeholder-opaque-black p-3 mt-3"
                type="text"
                rows="4"
                placeholder="MESSAGE"
                {...register("message", {
                  required: true,
                  maxLength: 2000,
                })}
              />
              {errors.message && (
                <p className="text-red mt-1">
                  {errors.message.type === "required" &&
                    "This field is Required"}
                  {errors.message.type === "maxLength" &&
                    "Max length is 2000 char"}
                </p>
              )}

              <button
                className="p-4 w-full md:w-[250px] bg-light-blue font-semibold text-white mt-5 hover:bg-deep-blue hover:text-white transition duration-500"
                type="submit"
              >
                Send Me
              </button>
            </form>
          </motion.div>
        </div>
      </section>
    );


}

export default Contact;