import { useState } from "react";
import useMediaQuery from "../hooks/useMediaQuery";
import { HiMenuAlt3, HiOutlineX } from "react-icons/hi";
import Link from "./Link";

const Navbar = ({ selectedPage, setselectedPage, isTopOfPage}) => {
  const [isMenuToggled, setisMenuToggled] = useState(false);
    const navbarBackground = isTopOfPage ? "" : "bg-deep-blue text-white";
  const isAboveSmallScreens = useMediaQuery("(min-width: 768px)");
  const Links = ["Home", "Skills", "Projects", "Testimonials", "Contact"];

  return (
    <nav className={`${navbarBackground} z-40 w-full fixed top-0 py-6`}>
      <div className="flex items-center justify-between mx-auto w-5/6 ">
        <h4 className="font-playfair text-3xl font-bold">MY</h4>

        {isAboveSmallScreens ? (
          <div className="flex justify-between gap-16 font-opensans text-sm font-semibold">
            {Links.map((linkpage) => (
              <Link
                page={linkpage}
                selectedPage={selectedPage}
                setselectedPage={setselectedPage}
              />
            ))}
          </div>
        ) : (
          <button onClick={() => setisMenuToggled(!isMenuToggled)}>
            <HiMenuAlt3 size={35} />
          </button>
        )}

        {/* {mobile screen menu} */}
        {!isAboveSmallScreens && isMenuToggled && (
          <div className="fixed right-0 bottom-0 h-full bg-light-blue w-[300px]">
            <div className="flex justify-end p-12">
              <button onClick={() => setisMenuToggled(!isMenuToggled)}>
                <HiOutlineX size={35} />
              </button>
            </div>

            <div className="flex flex-col gap-10 ml-[33%] text-2xl text-white scale-up-center">
              {Links.map((linkpage) => (
                <Link
                  page={linkpage}
                  selectedPage={selectedPage}
                  setselectedPage={setselectedPage}
                />
              ))}
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
