import LineGradient from "../components/LineGradient";
import { motion } from "framer-motion";
import skillsImage from "../assets/skillsImage.png";
import { skills, skills2 } from ".";

const MySkills = () => {
  return (
    <section id="skills" className="pt-10 pb-24">
      <div className="md:flex md:justify-between md:gap-16 mt-27 md:mt-32">
        <motion.div
          className="md:w-1/3 basis-2/5"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: -200 },
            visible: { opacity: 1, x: 0 },
          }}
        >
          <p className="font-playfair font-semibold text-4xl mb-5">
            MY<span className="text-light-blue">SKILLS</span>
          </p>
          <LineGradient width="w-1/3" />
          <p className="mt-10 mb-7">
            As a software developer specializing in web development and data
            structures, I possess a diverse skill set that enables me to design
            and implement high-quality software solutions.
          </p>
        </motion.div>

        <div className="mt-16 md:mt-0 basis-3/5">
          <img src={skills} alt="skills" />
        </div>
      </div>
      <div className="md:flex md:justify-between mt-16 gap-32">
        <motion.div
          className="md:w-1/3 mt-10"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: 50 },
            visible: { opacity: 1, y: 0 },
          }}
        >
          <div className="relative h-32">
            <div className="z-10">
              <p className="font-playfair font-semibold text-5xl text-center md:text-white">
                01
              </p>
              <p className="font-playfair font-semibold text-3xl text-center mt-3 md:text-white">
                Experience
              </p>
            </div>
            <div className="w-1/2 md:w-full rounded-br-full rounded-tl-full h-32 bg-gradient-background absolute right-0 top-0 z-[-1]" />
          </div>
          <p className="mt-5">
            As a junior, I have honed my skills in programming, problem-solving,
            and teamwork, while maintaining a strong desire to learn and grow in
            my field.
          </p>
        </motion.div>

        <motion.div
          className="md:w-1/3 mt-10"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: 50 },
            visible: { opacity: 1, y: 0 },
          }}
        >
          <div className="relative h-32">
            <div className="z-10">
              <p className="font-playfair font-semibold text-5xl text-center md:text-white">
                02
              </p>
              <p className="font-playfair text-center font-semibold text-3xl mt-3 md:text-white">
                Innovative
              </p>
            </div>
            <div className="w-1/2 md:w-full rounded-br-full rounded-tl-full h-32 bg-gradient-background absolute right-0 top-0 z-[-1]" />
          </div>
          <p className="mt-5">
            As a junior developer, I have demonstrated my innovative approach by
            contributing novel ideas to my team, utilizing my creativity and
            problem-solving skills to improve processes and enhance user
            experiences.
          </p>
        </motion.div>

        <motion.div
          className="md:w-1/3 mt-10"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ delay: 0.4, duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: 50 },
            visible: { opacity: 1, y: 0 },
          }}
        >
          <div className="relative h-32">
            <div className="">
              <p className="font-playfair font-semibold text-5xl text-center md:text-white">
                03
              </p>
              <p className="font-playfair font-semibold text-3xl mt-3 text-center md:text-white">
                Imaginative
              </p>
            </div>
            <div className="w-1/2 md:w-full rounded-br-full rounded-tl-full h-32 bg-gradient-background absolute right-0 top-0 z-[-1]" />
          </div>
          <p className="mt-5">
            As a junior developer, I have a vivid imagination that fuels my
            passion for creating unique and innovative solutions. I see
            challenges as opportunities to think outside the box and come up
            with creative approaches to solve complex problems.
          </p>
        </motion.div>
      </div>
    </section>
  );
};

export default MySkills;
