import LineGradient from "../components/LineGradient";
import { motion } from 'framer-motion';

const Testimonials = () => {
    const testimonialStyles = 'mx-auto relative max-w-[400px] h-[350px] flex flex-col justify-end p-16 mt-48 before:absolute before:top-[-120px] before:-ml-[110px] before:left-1/2';

    return (
      <section id="testimonials" className="pt-15 pb-16">
        <motion.div
          className="md:w-1/3 md:ml-10 md:mt-20"
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: -50 },
            visible: { opacity: 1, x: 0 },
          }}
        >
          <p className="font-playfair font-semibold text-4xl text-center md:text-left mb-5 text-deep-blue">
            Testimonials
          </p>
          <LineGradient width="mx-auto md:ml-0 w-2/4" />
          <p className="mt-10 text-center md:text-left">
            Passionate Junior Web Developer and skilled in data structures and
            algorithms, delivering efficient and user-friendly websites. See
            testimonials below. Let's create something amazing together!
          </p>
        </motion.div>

        <div className="md:flex md:justify-between gap-8">
          <motion.div
            className={`bg-green ${testimonialStyles} before:content-person1 `}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.5 }}
            variants={{
              hidden: { opacity: 0, scale: 0.8 },
              visible: { opacity: 1, scale: 1 },
            }}
          >
            <p className="font-playfair text-6xl"></p>
            <p className="text-center text-xl">
              "As a junior developer, I have a vivid imagination that"
            </p>
          </motion.div>

          <motion.div
            className={`bg-yellow ${testimonialStyles} before:content-person2`}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            transition={{ delay: 0.2, duration: 0.5 }}
            variants={{
              hidden: { opacity: 0, scale: 0.8 },
              visible: { opacity: 1, scale: 1 },
            }}
          >
            <p className="font-playfair text-6xl"></p>
            <p className="text-center text-xl">
              "As a junior developer, I have a vivid imagination that fuels my"
            </p>
          </motion.div>

          <motion.div
            className={`bg-purple ${testimonialStyles} before:content-person3`}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.5 }}
            transition={{ delay: 0.4, duration: 0.5 }}
            variants={{
              hidden: { opacity: 0, scale: 0.8 },
              visible: { opacity: 1, scale: 1 },
            }}
          >
            <p className="font-playfair text-6xl"></p>
            <p className="text-center text-xl">
              "As a junior developer, I have a vivid imagination that fuels my""
            </p>
          </motion.div>
        </div>
      </section>
    );
}
 
export default Testimonials;